export function dynamicValue(value, loading) {
    if (loading) {
        return { status: "loading", value };
    }
    return value ? { status: "available", value } : { status: "unavailable", value: undefined };
}
export function actionValue(canExecute = true, isExecuting = false) {
    return { canExecute, isExecuting, execute: jest.fn() };
}
export class EditableValueBuilder {
    constructor() {
        this.editableValue = {
            value: undefined,
            displayValue: "",
            status: "available",
            validation: undefined,
            readOnly: false,
            formatter: {
                format: jest.fn(name => `Formatted ${name}`),
                parse: jest.fn(),
                getFormatPlaceholder: jest.fn()
            },
            setValidator: jest.fn(),
            setValue: jest.fn((value) => this.withValue(value)),
            setTextValue: jest.fn(),
            setFormatter: jest.fn()
        };
    }
    withValue(value) {
        this.editableValue.value = value;
        this.editableValue.displayValue = this.editableValue.formatter.format(value);
        return this;
    }
    isReadOnly() {
        this.editableValue.readOnly = true;
        return this;
    }
    isLoading() {
        this.editableValue.status = "loading";
        return this.isReadOnly();
    }
    isUnavailable() {
        this.editableValue.status = "unavailable";
        return this.isReadOnly();
    }
    withValidation(validation) {
        this.editableValue.validation = validation;
        return this;
    }
    withUniverse(...values) {
        this.editableValue.universe = values;
        return this;
    }
    build() {
        return this.editableValue;
    }
}
